import React, { useState, useEffect } from 'react';

import { FiArrowRight, FiCheckCircle } from 'react-icons/fi';

import { QRCodeCanvas } from 'qrcode.react'; // ‚úÖ Lightweight QR library
import { supabase } from '../../../../database/supabaseClient';
import { events } from '../../../../content/events';

const i18n =  events.test

const EventPayment: React.FC = () => {
  const [language, setLanguage] = useState<'EN' | 'VN'>('EN');
  const [loading, setLoading] = useState(false);
  const [isPaid, setIsPaid] = useState(false);
  const [user, setUser] = useState<any>(null);
  const [qrData, setQrData] = useState<any>(null);
  const [transactionId, setTransactionId] = useState<string | null>(null);
  const [testLoading, setTestLoading] = useState(false);
  const t = i18n[language];

  // ‚úÖ Load logged-in user
  useEffect(() => {
    supabase.auth.getUser().then(({ data }) => {
      if (data?.user) {
        setUser(data.user);
        console.log(`‚úÖ Logged in as ${data.user.email} (${data.user.id})`);
      } else {
        console.log('‚ùå No user logged in');
      }
    });
  }, []);

  // üëÇ Watch for live VietQR callback updates
  useEffect(() => {
    if (!user) return;

    console.log('üëÇ Setting up realtime listener for VietQR callbacks...');
    const channel = supabase
      .channel('public:messages')
      .on(
        'postgres_changes',
        { event: 'INSERT', schema: 'public', table: 'messages' },
        (payload) => {
          try {
            const { event_type, payload: eventPayload } = payload.new;
            if (event_type === 'vietqr_callback') {
              const orderId = eventPayload?.orderId ?? '';
              console.log('üîç Checking callback...');
              console.log('‚û°Ô∏è orderId:', orderId);
              console.log('‚û°Ô∏è current user:', user.id);

              if (!orderId) {
                console.warn('‚ö†Ô∏è Missing orderId; ignoring.');
                return;
              }

              if (orderId !== user.id) {
                console.warn('‚ö†Ô∏è orderId does not match current user.');
                return;
              }

              console.log('‚úÖ Matched callback for current user!');
              console.log('üì¶ Payload:', JSON.stringify(eventPayload, null, 2));

              // Mark payment as confirmed
              setIsPaid(true);
              setQrData(null); // Hide QR + buttons
              setTransactionId(eventPayload.transactionid || null);
            }
          } catch (err) {
            console.error('‚ö†Ô∏è Error parsing payload:', err);
          }
        }
      )
      .subscribe((status) => console.log(`üì° Channel status: ${status}`));

    return () => {
      console.log('üßπ Unsubscribing from realtime listener...');
      supabase.removeChannel(channel);
    };
  }, [user]);

  // === Create VietQR Payment Intent ===
  const handlePayment = async () => {
    if (!user) {
      alert('Please log in first.');
      return;
    }

    const endpoint = 'https://integrations.tinhhoaviet.org.vn/vietqr/payment-intent';
    try {
      setLoading(true);
      console.log('üí≥ Initiating payment for user:', user.id);

      const res = await fetch(endpoint, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ user_id: user.id }),
      });

      if (!res.ok) throw new Error('Payment request failed.');
      const data = await res.json();

      console.log('üí≥ VietQR API Response:', data);

      if (data.qrLink || data.qrDataURL || data.qrCode) {
        setQrData(data);
      } else {
        alert('Payment QR/link not received.');
      }
    } catch (err) {
      console.error('‚ùå Payment Error:', err);
      alert('An error occurred while initiating payment.');
    } finally {
      setLoading(false);
    }
  };

  // === Test Payment Simulation ===
  const handleTestPayment = async () => {
    if (!qrData) {
      alert('No QR data available.');
      return;
    }

    const payload2 = {
      bankAccount: qrData.bankAccount || '0369053640',
      content: qrData.content || 'TestPayment',
      amount: qrData.amount || 150000,
      bankCode: qrData.bankCode || 'MB',
      transType: 'C',
    };

    console.log('üßæ Sending payload2:', payload2);
    setTestLoading(true);

    try {
      const res = await fetch('https://integrations.tinhhoaviet.org.vn/vietqr/test-success', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload2),
      });

      const result = await res.text();
      console.log('‚úÖ Response from test-success:', result);
    } catch (err) {
      console.error('‚ùå Failed to post to /vietqr/test-success:', err);
    } finally {
      setTestLoading(false);
    }
  };

  return (
    <div className="auth-shell">
      <aside className="brand-side">
        <div className="brand-sheen" />
        <div className="brand-pattern" />
        <div className="brand-wrap">
          <h1 className="brand-name">{t.brandTitle}</h1>
          <p className="brand-tag">{t.tagline}</p>
          <ul className="brand-features">
            {t.features.map((f, i) => (
              <li key={i} className="feature">
                <span className="tick">‚úì</span><span>{f}</span>
              </li>
            ))}
          </ul>
        </div>
      </aside>

      <main className="form-side">
        <header className="form-top">
          <div className="lang-switch">
            <button
              className={`lang ${language === 'EN' ? 'active' : ''}`}
              onClick={() => setLanguage('EN')}
            >
              {t.langEN}
            </button>
            <span className="mid">|</span>
            <button
              className={`lang ${language === 'VN' ? 'active' : ''}`}
              onClick={() => setLanguage('VN')}
            >
              {t.langVN}
            </button>
          </div>
        </header>

        <section className="form-wrap" style={{ color: 'var(--ink)' }}>
          <div className="intro">
            <h2 className="title">{t.title}</h2>
            <p className="subtitle">{t.subtitle}</p>
          </div>

          <div className="auth-form" style={{ textAlign: 'center', color: 'var(--ink)' }}>
            <div className="field">
              <h3 style={{ color: 'var(--gold-2)', fontSize: 22, marginBottom: 6 }}>
                {t.eventName}
              </h3>
              <p>{t.date}</p>
              <p>{t.location}</p>
              <p style={{ fontWeight: 700, marginTop: 10 }}>{t.price}</p>
            </div>

            {/* === Payment confirmed === */}
            {isPaid ? (
              <div
                style={{
                  marginTop: 20,
                  color: 'var(--gold-2)',
                  fontWeight: 700,
                  display: 'flex',
                  flexDirection: 'column',
                  alignItems: 'center',
                  justifyContent: 'center',
                  gap: 6,
                }}
              >
                <div style={{ display: 'flex', alignItems: 'center', gap: 8 }}>
                  <FiCheckCircle size={22} />
                  {t.success}
                </div>
                {transactionId && (
                  <p style={{ color: 'var(--ink)', marginTop: 4 }}>
                    Transaction ID: <strong>{transactionId}</strong>
                  </p>
                )}
              </div>
            ) : (
              <>
                {/* QR + Buttons only while waiting for payment */}
                {qrData && (qrData.qrDataURL || qrData.qrCode) && (
                  <div style={{ marginTop: 20 }}>
                    <QRCodeCanvas
                      value={qrData.qrDataURL || qrData.qrCode}
                      size={220}
                      bgColor="#ffffff"
                      fgColor="#000000"
                      includeMargin={true}
                    />
                    <p style={{ marginTop: 12, color: 'var(--gold-2)' }}>
                      Scan this code to complete your payment.
                    </p>
                  </div>
                )}

                {!qrData ? (
                  <button
                    className="cta"
                    onClick={handlePayment}
                    disabled={loading || !user}
                    style={{
                      opacity: loading || !user ? 0.6 : 1,
                      cursor: !user ? 'not-allowed' : 'pointer',
                    }}
                  >
                    <span>
                      {!user ? 'Login required' : loading ? t.loading : t.payBtn}
                    </span>
                    {!loading && user && <FiArrowRight className="arrow" />}
                  </button>
                ) : (
                  <button
                    className="cta"
                    onClick={handleTestPayment}
                    disabled={testLoading}
                    style={{
                      backgroundColor: '#00b37a',
                      marginTop: 10,
                      opacity: testLoading ? 0.7 : 1,
                      display: 'flex',
                      alignItems: 'center',
                      justifyContent: 'center',
                      fontWeight: 600,
                    }}
                  >
                    {testLoading ? '‚è≥' : t.testBtn}
                  </button>
                )}
              </>
            )}
          </div>
        </section>
      </main>
    </div>
  );
};

export default EventPayment;